body {
  margin: 20px;
  font-family: sans-serif;
}
main {
  display: flex;
  flex-wrap: wrap;
}

.profile-card {
  display: flex;
  margin-bottom: 20px;
  min-width: 400px;
}
.profile-avatar {
  border-radius: 50%;
  border: 3px solid black;
  background-color: black;
}
.profile-info {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.profile-name {
  font-size: 1.5em;
  color: #001f3f;
  margin: 0;
}

.profile-email {
  font-size: 1.3em;
  color: blue;
  margin: 0;
}
